import { get } from 'lodash';

import { fixNesting, fixMultiplicity, addSupportedValue } from '../../../utils.js';

function createContractList(data) {

  // if we can create a contractList we return the contractlist
  var result = get(data, 'Body.ContractList.ContractData');

  result = fixMultiplicity(result);

  // fix the double nesting
  result = fixNesting(result);

  result = addSupportedValue(result);

  return result;
}

export { createContractList };