import { get } from 'lodash';

export { createInvestmentAccountPerformanceDetails };

function createInvestmentAccountPerformanceDetails(data) {

  // our data for our graphs
  var graphData = get(data, 'PerformanceGraph');
  var performance = {};

  // GRAPH: vermogen & rendement
  performance.capital = get(graphData, '[0].Performances.DataPoint');
  performance.efficiency = get(graphData, '[2].Performances.DataPoint');

  return performance;
}