import { endpoints } from '../_endpoints.js';
import { post } from "../_http";

function getUserListView(companyId) {
  var payloadData = {
    GetUserListView_Request: {
      user_organisation_id: companyId,
      data_organisation_id: companyId
    }
  };

  return post(endpoints.CORPORATE.GET_USER_LIST_VIEW, payloadData);
}

export { getUserListView };