import _Promise from 'babel-runtime/core-js/promise';
import { healthcareService } from '../service.js';
import { NNAPIError } from './error/NNAPIError';
import { NNIllegalArgumentError } from './error/NNIllegalArgumentError';
import { get } from 'lodash';
import { healthcareFactory } from '../factories.js';

export { getPolicyDetails };

function getPolicyDetails(accountId) {
  if (!accountId || typeof accountId !== 'string') {
    return _Promise.reject(new NNIllegalArgumentError('healthcare.getPolicyDetails', 'accountId', accountId));
  }

  return healthcareService.getPolicyDetailsHealthInsuranceForCustomer(accountId).then(function (response) {
    //Healthcare service is from Ohra and does not (yet) conform to NN standards.
    //Therefore it is not possible to use our generic response parser.
    var status = get(response, 'GetPolicyDetailsHealthInsurance_Response.Result.Status');
    var errorList = get(response, 'GetPolicyDetailsHealthInsurance_Response.Result.ErrorList');
    if (status !== 'OK' && errorList) {
      var serviceContext = 'HealthInsurance';
      var actionName = 'GetPolicyDetailsHealthInsurance';
      throw new NNAPIError(status, errorList, serviceContext, actionName);
    } else {
      return healthcareFactory.createHealthcareDetails(response);;
    }
  });
}