import _JSON$stringify from 'babel-runtime/core-js/json/stringify';
import { endpoints, getAbsoluteUrl, fetch } from '../_endpoints';
import { NNServiceError } from '../error/NNServiceError';

export { getTotalBalance };

function getTotalBalance(accountDataArr) {

  var input = [];
  accountDataArr.forEach(function (item) {
    input.push({ REKNR: item.contractId, VTA_id: item.vtaId, SRBPID: item.sapId });
  });

  var payloadData = {
    GetAccountsOverview3_Request: {
      AL: {
        GetGRANDTOTREKBAL: true,
        UserName: 'NNAPP'
      },
      DP: input
    }
  };

  var opts = {
    method: 'POST',
    body: _JSON$stringify(payloadData)
  };

  return fetch(getAbsoluteUrl(endpoints.BANK.GET_ACCOUNTS_OVERVIEW), opts).then(function (response) {
    // Check if response is in the 2xx range
    if (response.ok) {
      return response.json();
    }

    // Request failed (status was not in the 2xx range). Let the application layer deal with this.
    throw new NNServiceError(response.status, endpoints.BANK.GET_ACCOUNTS_OVERVIEW);
  });
}