export { getFile };

function getFile(options) {
  var request = JSON.parse(options.body);
  var accountNr = request.GetUserDataCustomer_Request.Body.UserName;

  switch (accountNr) {
    case 'accountNr':
      return obj1;
    case 'creditCard':
      return creditcard1;
    case 'creditCardNested':
      return creditcard2;
    default:
      return obj1;
  }
}

