import _typeof from 'babel-runtime/helpers/typeof';
import { get } from 'lodash';

import { fixMultiplicity } from '../../../utils.js';

export { createSavingsDetails };

function createSavingsDetails(data) {

  if (!get(data, 'DPGRP')) {
    throw new Error('Incorrect body GetAccountsOverview');
  }

  var result = get(data, 'DPGRP');
  result = result.DP;
  result = fixMultiplicity(result);
  result = fixNaming(result);

  return result;
}

// Fix name of "internetsparen deposito" to Depositosparen
function fixNaming(details) {
  details.forEach(function (DP) {
    if (_typeof(DP.IBAN) === 'object' && DP.SPRDSRT === 'N300') {
      DP.SRTDEP_NAAM = 'Depositosparen';
      DP.SPRDSRT_NAAM = 'Depositosparen';
    }
  });

  return details;
}