function getContractIDsByProductcode(list, productcodes) {

  var contractIDs = [];
  var codes = Array.isArray(productcodes) ? productcodes : [productcodes];

  list.forEach(function (item) {
    if (codes.includes(item.PRODUCTCODE) && item.VTA_Contract_id.length === 18) {
      contractIDs.push(item.VTA_Contract_id);
    }
  });

  return contractIDs;
}

export { getContractIDsByProductcode };