import { get } from 'lodash';

import { createContractList } from './createContractList.js';

export { createFlatContractList };

function createFlatContractList(data) {
  var result = createContractList(data);
  var contractsWithNesting = result.filter(function (contract) {
    if (get(contract, 'ContractSubParts.ContractSubPart')) {
      return contract;
    }
  });

  if (contractsWithNesting[0] === null) {
    return result;
  }

  contractsWithNesting.forEach(function (contract) {
    subProducts = contract.ContractSubParts.ContractSubPart;

    result = result.concat(subProducts);

    delete contract.ContractSubParts.ContractSubPart;
  });

  return result;
}