import _JSON$stringify from 'babel-runtime/core-js/json/stringify';
import _Promise from 'babel-runtime/core-js/promise';
import { forEach } from 'lodash';

import { endpoints, getAbsoluteUrl, fetch } from '../_endpoints';
import { isIbanNumber } from '../../utils.js';

export { getPaymentSchedulesForCustomer };

function getPaymentSchedulesForCustomer(contractNumbers) {

  if (!contractNumbers || !Array.isArray(contractNumbers)) {
    return _Promise.reject(new Error('contractNumbers are missing or not an Array'));
  }

  var payload = getPayload(contractNumbers);

  var payloadData = {
    GetPaymentSchedulesForCustomer_Request: {
      AL: {
        UserName: 'NNAPP'
      },
      DP: payload
    }
  };

  var opts = {
    method: 'POST',
    body: _JSON$stringify(payloadData)
  };

  return fetch(getAbsoluteUrl(endpoints.BANK.GET_PAYMENT_SCHEDULES), opts).then(function (response) {
    if (response.ok) {
      return response.json();
    }

    throw new NNServiceError(response.status, endpoints.BANK.GET_PAYMENT_SCHEDULES);
  });
}

function getPayload(contractNumbers) {

  var payload = [];

  forEach(contractNumbers, function (item) {
    if (isIbanNumber(item)) {
      payload.push({ IBAN: item });
    } else {
      payload.push({ REKNR: item });
    }
  });

  return payload;
}