import _JSON$stringify from 'babel-runtime/core-js/json/stringify';
import { endpoints, getAbsoluteUrl, fetch } from '../_endpoints';
import { NNServiceError } from '../error/NNServiceError';

export { handlePayment };

function handlePayment(vtaID, REKNR, transactionOptions) {

    var payloadData = {
        HandlePayment_Request: {
            AL: {
                REQTYPE: '2',
                UserName: 'NNAPP'
            },
            DP: {
                DPAJ: {
                    AJ: {
                        AFBYBDR: transactionOptions.amount,
                        AFBYOMS: transactionOptions.description,
                        AFBYTIBAN: transactionOptions.counterIBAN,
                        AFBYTIBIC: transactionOptions.counterIBIC,
                        AFBYTRNAAM: transactionOptions.counterName
                    }
                },
                REKNR: REKNR,
                VTA_id: vtaID
            }
        }
    };

    var opts = {
        method: 'POST',
        body: _JSON$stringify(payloadData)
    };

    return fetch(getAbsoluteUrl(endpoints.BANK.HANDLE_PAYMENT), opts).then(function (response) {
        // Check if response is in the 2xx range
        if (response.ok) {
            return response.json();
        }

        // Request failed (status was not in the 2xx range). Let the application layer deal with this.
        throw new NNServiceError(response.status, endpoints.BANK.HANDLE_PAYMENT);
    });
}