import _Promise from 'babel-runtime/core-js/promise';
import _typeof from 'babel-runtime/helpers/typeof';
import { get } from 'lodash';

import { paymentService } from '../service.js';
import { paymentFactory } from '../factories.js';
import { fixMultiplicity } from '../utils.js';
import { NNAPIError } from './error/NNAPIError';
import { NNIllegalArgumentError } from './error/NNIllegalArgumentError';
import { ServiceResponseParser } from './utils/ServiceResponseParser';

var getLink = getPaymentLink;
var getStatus = getPaymentStatus;
var getOverview = getPaymentOverview;

export { getPaymentOverview, getPaymentList, getPayInfoFromPayment, getLink, getStatus, getOverview };

function getPaymentOverview() {
  return paymentService.getAccountStatementCustomerDataForCustomer().then(function (response) {
    var parsed = new ServiceResponseParser(response);
    if (parsed.status !== ServiceResponseParser.OK) {
      throw new NNAPIError(parsed.status, parsed.errorList, parsed.serviceContext, parsed.actionName);
    }

    var result = fixMultiplicity(get(parsed.result, 'ContractData'));
    result.forEach(function (paymentContract) {
      if (paymentContract.Items) {
        fixMultiplicity(paymentContract.Items);
      }
    });

    return result;
  });
}

function getPaymentList(paymentContracts) {
  return paymentFactory.createPaymentList(paymentContracts);
}

function getPayInfoFromPayment(paymentContract, payment) {
  var paymentInfo = {
    lastName: '',
    policyNumber: '',
    documentNumber: '',
    repetitionItem: '',
    documentItemNumber: '',
    subItem: '',
    description: '',
    date: _getDate(),
    concerning: '',
    expirationDate: _getExpirationDate(),
    returnURL: 'nnserviceappacc://contracts'
  };

  paymentInfo.lastName = get(paymentContract, 'PersonName.FullName');
  paymentInfo.policyNumber = get(paymentContract, 'Contract.PolicyNumber');
  paymentInfo.documentNumber = get(payment, 'DocumentNumber');
  paymentInfo.repetitionItem = get(payment, 'RepetitionItem');
  paymentInfo.documentItemNumber = get(payment, 'ItemNumber');
  paymentInfo.subItem = get(payment, 'SubItemNumber');
  paymentInfo.description = get(payment, 'SubTransactionDescription');
  paymentInfo.concerning = get(payment, 'Concerning');

  return paymentInfo;
}

function getPaymentLink(paymentInfo) {

  if (!paymentInfo || (typeof paymentInfo === 'undefined' ? 'undefined' : _typeof(paymentInfo)) !== 'object') {
    return _Promise.reject(new NNIllegalArgumentError('payment.getPaymentLink', 'paymentInfo', paymentInfo));
  }

  if (!paymentInfo.lastName) {
    return _Promise.reject(new NNIllegalArgumentError('payment.getPaymentLink', 'paymentInfo.lastName', paymentInfo.lastName));
  }

  if (!paymentInfo.policyNumber) {
    return _Promise.reject(new NNIllegalArgumentError('payment.getPaymentLink', 'paymentInfo.policyNumber', paymentInfo.policyNumber));
  }

  if (!paymentInfo.documentNumber) {
    return _Promise.reject(new NNIllegalArgumentError('payment.getPaymentLink', 'paymentInfo.documentNumber', paymentInfo.documentNumber));
  }

  if (!paymentInfo.repetitionItem) {
    return _Promise.reject(new NNIllegalArgumentError('payment.getPaymentLink', 'paymentInfo.repetitionItem', paymentInfo.repetitionItem));
  }

  if (!paymentInfo.documentItemNumber) {
    return _Promise.reject(new NNIllegalArgumentError('payment.getPaymentLink', 'paymentInfo.documentItemNumber', paymentInfo.documentItemNumber));
  }

  if (!paymentInfo.subItem) {
    return _Promise.reject(new NNIllegalArgumentError('payment.getPaymentLink', 'paymentInfo.subItem', paymentInfo.subItem));
  }

  if (!paymentInfo.description) {
    return _Promise.reject(new NNIllegalArgumentError('payment.getPaymentLink', 'paymentInfo.description', paymentInfo.description));
  }

  if (!paymentInfo.date) {
    return _Promise.reject(new NNIllegalArgumentError('payment.getPaymentLink', 'paymentInfo.date', paymentInfo.date));
  }

  if (!paymentInfo.concerning) {
    return _Promise.reject(new NNIllegalArgumentError('payment.getPaymentLink', 'paymentInfo.concerning', paymentInfo.concerning));
  }

  if (!paymentInfo.expirationDate) {
    return _Promise.reject(new NNIllegalArgumentError('payment.getPaymentLink', 'paymentInfo.expirationDate', paymentInfo.expirationDate));
  }

  if (!paymentInfo.returnURL) {
    return _Promise.reject(new NNIllegalArgumentError('payment.getPaymentLink', 'paymentInfo.returnURL', paymentInfo.returnURL));
  }

  return paymentService.getPaymentReferenceAndPayLink(paymentInfo).then(function (response) {
    var parsed = new ServiceResponseParser(response);
    if (parsed.status !== ServiceResponseParser.OK) {
      throw new NNAPIError(parsed.status, parsed.errorList, parsed.serviceContext, parsed.actionName);
    }

    return parsed.result;
  });
}

function getPaymentStatus(paymentID, bankNumber, bankSystem) {
  if (!paymentID || typeof paymentID !== 'string') {
    return _Promise.reject(new NNIllegalArgumentError('payment.getPaymentStatus', 'paymentID', paymentID));
  }

  if (!bankNumber || typeof bankNumber !== 'string') {
    return _Promise.reject(new NNIllegalArgumentError('payment.getPaymentStatus', 'bankNumber', bankNumber));
  }

  if (!bankSystem || typeof bankSystem !== 'string') {
    return _Promise.reject(new NNIllegalArgumentError('payment.getPaymentStatus', 'bankSystem', bankSystem));
  }

  return paymentService.getPaymentStatusForCustomer(paymentID, bankNumber, bankSystem).then(function (response) {
    var parsed = new ServiceResponseParser(response);
    if (parsed.status !== ServiceResponseParser.OK) {
      throw new NNAPIError(parsed.status, parsed.errorList, parsed.serviceContext, parsed.actionName);
    }

    return parsed.result;
  });
}

function _getDate() {
  return new Date().toJSON().slice(0, 10);
}

function _getExpirationDate() {
  var todayDate = new Date();
  todayDate.setDate(todayDate.getDate() + 1);

  return todayDate.toJSON().slice(0, 10);
}