function getTodayDate() {
  var futureDate = new Date();
  futureDate.setMonth(futureDate.getMonth() + FUTURE_MONTH_PAYMENTS);
  futureDate.toDateString();
  return futureDate.toJSON().slice(0, 10);
}

// date from = today date - AMOUNT_OF_PAYMENT_YEARS
function getFromDate() {
  var fromDate = new Date();
  fromDate.setFullYear(fromDate.getFullYear() - AMOUNT_OF_PAYMENT_YEARS);
  return fromDate.toJSON().slice(0, 10);
}