import { get } from 'lodash';

function createCustomer(data) {

  if (!get(data, 'GetUserDataCustomer_Response.Body.BusinessPartnerData')) {
    // error
    throw Error('Service Error:: getUserDataCustomer');
  }

  var result = get(data, 'GetUserDataCustomer_Response.Body.BusinessPartnerData');

  return result;
}

export { createCustomer };