import _Promise from 'babel-runtime/core-js/promise';
import { NNAPIError } from './error/NNAPIError';
import { NNIllegalArgumentError } from './error/NNIllegalArgumentError';
import { lifeService } from '../service.js';
import { ServiceResponseParser } from './utils/ServiceResponseParser';

export { getDetailsVLife, getLifeInvestmentDetails };

function getDetailsVLife(contractId, VTAId) {

  if (!contractId || typeof contractId !== 'string') {
    return _Promise.reject(new NNIllegalArgumentError('getDetailsVLife', 'contractId', contractId));
  }

  if (!VTAId || typeof VTAId !== 'string') {
    return _Promise.reject(new NNIllegalArgumentError('getDetailsVLife', 'VTAId', VTAId));
  }

  return lifeService.getPolicyDetailsVLifeTLForCustomer(contractId, VTAId).then(function (response) {
    var parsed = new ServiceResponseParser(response);
    if (parsed.status !== ServiceResponseParser.OK) {
      throw new NNAPIError(parsed.status, parsed.errorList, parsed.serviceContext, parsed.actionName);
    }

    return parsed.result;
  });
}

function getLifeInvestmentDetails(contractId, VTAId) {

  if (!contractId || typeof contractId !== 'string') {
    return _Promise.reject(new NNIllegalArgumentError('getLifeInvestmentDetails', 'contractId', contractId));
  }

  if (!VTAId || typeof VTAId !== 'string') {
    return _Promise.reject(new NNIllegalArgumentError('getLifeInvestmentDetails', 'VTAId', VTAId));
  }

  return lifeService.getPolicyDetailsJuiceULForCustomer(contractId, VTAId).then(function (response) {
    var parsed = new ServiceResponseParser(response);
    if (parsed.status !== ServiceResponseParser.OK) {
      throw new NNAPIError(parsed.status, parsed.errorList, parsed.serviceContext, parsed.actionName);
    }

    return parsed.result;
  });
}