import _JSON$stringify from 'babel-runtime/core-js/json/stringify';
import { get } from 'lodash';

import { endpoints, getAbsoluteUrl, fetch } from '../_endpoints';
import { NNServiceError } from '../error/NNServiceError';

export { getPaymentReferenceAndPayLink };

function getPaymentReferenceAndPayLink(paymentInfo) {

  var payloadData = {
    GetPaymentReferenceAndPayLink_Request: {
      Debtor: {
        LastName: get(paymentInfo, 'lastName'),
        Partner: 'NNAPP'
      },
      Contract: get(paymentInfo, 'policyNumber'),
      PayableItems: {
        DocumentNumber: get(paymentInfo, 'documentNumber'),
        RepetitionItem: get(paymentInfo, 'repetitionItem'),
        DocumentItemNumber: get(paymentInfo, 'documentItemNumber'),
        SubItem: get(paymentInfo, 'subItem')
      },
      Invoice: {
        Description: get(paymentInfo, 'description'),
        Date: get(paymentInfo, 'date')
      },
      PaymentData: {
        BatchId: 'NN App',
        Concerning: get(paymentInfo, 'concerning'),
        ExpirationDate: get(paymentInfo, 'expirationDate'),
        ReturnURL: get(paymentInfo, 'returnURL')
      }
    }
  };

  // console.log('payload getPaymentReferenceAndPayLink::', payloadData);

  var opts = {
    method: 'POST',
    body: _JSON$stringify(payloadData)
  };

  return fetch(getAbsoluteUrl(endpoints.PAYMENTS.GET_PAYMENT_REFERENCE_AND_PAY_LINK), opts).then(function (response) {
    if (response.ok) {
      return response.json();
    }

    throw new NNServiceError(response.status, endpoints.PAYMENTS.GET_PAYMENT_REFERENCE_AND_PAY_LINK);
  });
}