import { endpoints, getAbsoluteUrl, fetch, base64encode } from '../_endpoints.js';
import { NNServiceError } from '../error/NNServiceError';

/**
 * @param {String} username;
 * @param {String} password;
 * @returns {unresolved}
 */
function login(username, password) {
	var opts = {
		method: 'GET',
		redirect: 'follow',
		headers: {
			'NN-Authorization': 'Basic ' + base64encode(username + ':' + password)
		}
	};

	return fetch(getAbsoluteUrl(endpoints.LOGIN), opts).then(function (response) {
		// Check if response is in the 2xx range
		if (response.ok) {
			return response.json();
		}

		// Request failed (status was not in the 2xx range). Let the application layer deal with this.
		throw new NNServiceError(response.status, endpoints.LOGIN);
	});
}

export { login };