// add supported (true/false) to contracts

import { getSupportedProducts } from '../supported-products.js';
import { treeWalker } from './treeWalker.js';

function addSupportedValue(list) {

  treeWalker(list, function (item) {
    //ignore category objects
    if (item.pseudo) {
      return;
    }

    if (getSupportedProducts().includes(item.PRODUCTCODE)) {
      item.isSupported = true;
      return;
    }

    item.isSupported = false;
  });

  return list;
}

export { addSupportedValue };