import { get, has } from 'lodash';

function createTicketData(data) {

  if (!has(data, 'PutPortalApplicationData_Response.PutResult')) {
    // error
    throw Error('Service Error:: putPortalApplicationData');
  }

  return get(data, 'PutPortalApplicationData_Response.PutResult');
}

export { createTicketData };