import _typeof from 'babel-runtime/helpers/typeof';
import _Promise from 'babel-runtime/core-js/promise';
import { savingsService } from '../service.js';
import { NNAPIError } from './error/NNAPIError';
import { NNIllegalArgumentError } from './error/NNIllegalArgumentError';
import { ServiceResponseParser } from './utils/ServiceResponseParser';
import { fixMultiplicity } from '../utils.js';

export { getDetails, getTotalBalance, getTransactions, getTransactionSettings, sendTransaction, getPaymentSchedules };

function getDetails(contractId, vtaId, sapId) {

  if (!contractId || typeof contractId !== 'string') {
    return _Promise.reject(new NNIllegalArgumentError('savings.getDetails', 'contractId', contractId));
  }

  if (!vtaId || typeof vtaId !== 'string') {
    return _Promise.reject(new NNIllegalArgumentError('savings.getDetails', 'vtaId', vtaId));
  }

  if (!sapId || typeof sapId !== 'string') {
    return _Promise.reject(new NNIllegalArgumentError('savings.getDetails', 'sapId', sapId));
  }

  return savingsService.getDetails(contractId, vtaId, sapId).then(function (response) {
    var parsed = new ServiceResponseParser(response);
    if (parsed.status !== ServiceResponseParser.OK) {
      throw new NNAPIError(parsed.status, parsed.errorList, parsed.serviceContext, parsed.actionName);
    }

    return parsed.result;
  });
}

function getTotalBalance(accountData) {
  if (!accountData || (typeof accountData === 'undefined' ? 'undefined' : _typeof(accountData)) !== 'object') {
    return _Promise.reject(new NNIllegalArgumentError('savings.getTotalBalance', 'accountData', accountData));
  }

  var accountDataArr = Array.isArray(accountData) ? accountData : [accountData];
  if (accountDataArr.filter(function (item) {
    return !item || (typeof item === 'undefined' ? 'undefined' : _typeof(item)) !== 'object' || !item.vtaId || typeof item.vtaId !== 'string' || !item.sapId || typeof item.sapId !== 'string' || !item.contractId || typeof item.contractId !== 'string';
  }).length > 0) {
    return _Promise.reject(new NNIllegalArgumentError('savings.getTotalBalance', 'accountData', accountDataArr));
  }

  return savingsService.getTotalBalance(accountDataArr).then(function (response) {
    var parsed = new ServiceResponseParser(response);

    if (parsed.status !== ServiceResponseParser.OK) {
      throw new NNAPIError(parsed.status, parsed.errorList, parsed.serviceContext, parsed.actionName);
    }

    return parsed.result;
  });
}

function getTransactions(contractID, AFBY_NR, STRDEP, vtaId, subContractID) {
  if (!contractID || typeof contractID !== 'string') {
    return _Promise.reject(new NNIllegalArgumentError('savings.getTransactions', 'contractID', contractID));
  }
  if (!STRDEP || typeof STRDEP !== 'string') {
    return _Promise.reject(new NNIllegalArgumentError('savings.getTransactions', 'STRDEP', STRDEP));
  }
  if (!vtaId || typeof vtaId !== 'string') {
    return _Promise.reject(new NNIllegalArgumentError('savings.getTransactions', 'vtaId', vtaId));
  }
  if (subContractID && typeof subContractID !== 'string') {
    return _Promise.reject(new NNIllegalArgumentError('savings.getTransactions', 'subContractID', subContractID));
  }

  return savingsService.getTransactionsService(contractID, AFBY_NR, STRDEP, vtaId, subContractID).then(function (response) {
    var parsed = new ServiceResponseParser(response);
    if (parsed.status !== ServiceResponseParser.OK) {
      throw new NNAPIError(parsed.status, parsed.errorList, parsed.serviceContext, parsed.actionName);
    }

    return parsed.result;
  });
}

function getTransactionSettings(vtaID, contractID) {
  if (!vtaID || typeof vtaID !== 'string') {
    return _Promise.reject(new NNIllegalArgumentError('savings.getTransactionSettings', 'vtaID', vtaID));
  }

  if (!contractID || typeof contractID !== 'string') {
    return _Promise.reject(new NNIllegalArgumentError('savings.getTransactionSettings', 'contractID', contractID));
  }

  // change of name because it's a transaction, not a payment :)
  return savingsService.getPaymentSettings(vtaID, contractID).then(function (response) {
    var parsed = new ServiceResponseParser(response);
    if (parsed.status !== ServiceResponseParser.OK) {
      throw new NNAPIError(parsed.status, parsed.errorList, parsed.serviceContext, parsed.actionName);
    }

    return parsed.result;
  });
}

function sendTransaction(vtaID, REKNR, transactionOptions) {
  if (!vtaID || typeof vtaID !== 'string') {
    return _Promise.reject(new NNIllegalArgumentError('savings.sendTransaction', 'vtaID', vtaID));
  }

  if (!REKNR || typeof REKNR !== 'string') {
    return _Promise.reject(new NNIllegalArgumentError('savings.sendTransaction', 'REKNR', REKNR));
  }

  if (!transactionOptions || (typeof transactionOptions === 'undefined' ? 'undefined' : _typeof(transactionOptions)) !== 'object') {
    return _Promise.reject(new NNIllegalArgumentError('savings.sendTransaction', 'transactionOptions', transactionOptions));
  }

  if (!transactionOptions.amount || typeof transactionOptions.amount !== 'string') {
    return _Promise.reject(new NNIllegalArgumentError('savings.sendTransaction', 'transactionOptions.amount', transactionOptions.amount));
  }

  // description is optional, but should be a string
  if (transactionOptions.description && typeof transactionOptions.description !== 'string') {
    return _Promise.reject(new NNIllegalArgumentError('savings.sendTransaction', 'transactionOptions.description', transactionOptions.description));
  }

  if (!transactionOptions.counterIBAN || typeof transactionOptions.counterIBAN !== 'string') {
    return _Promise.reject(new NNIllegalArgumentError('savings.sendTransaction', 'transactionOptions.counterIBAN', transactionOptions.counterIBAN));
  }

  if (!transactionOptions.counterIBIC || typeof transactionOptions.counterIBIC !== 'string') {
    return _Promise.reject(new NNIllegalArgumentError('savings.sendTransaction', 'transactionOptions.counterIBIC', transactionOptions.counterIBIC));
  }

  if (!transactionOptions.counterName || typeof transactionOptions.counterName !== 'string') {
    return _Promise.reject(new NNIllegalArgumentError('savings.sendTransaction', 'transactionOptions.counterName', transactionOptions.counterName));
  }

  return savingsService.handlePayment(vtaID, REKNR, transactionOptions).then(function (response) {
    var parsed = new ServiceResponseParser(response);
    if (parsed.status !== ServiceResponseParser.OK) {
      throw new NNAPIError(parsed.status, parsed.errorList, parsed.serviceContext, parsed.actionName);
    }

    return parsed.result;
  });
}

function getPaymentSchedules(contractNumbers) {
  if (!contractNumbers || (typeof contractNumbers === 'undefined' ? 'undefined' : _typeof(contractNumbers)) !== 'object') {
    return _Promise.reject(new NNIllegalArgumentError('savings.getPaymentSchedules', 'contractNumbers', contractNumbers));
  }

  return savingsService.getPaymentSchedulesForCustomer(contractNumbers).then(function (response) {
    var parsed = new ServiceResponseParser(response);
    if (parsed.status !== ServiceResponseParser.OK) {
      throw new NNAPIError(parsed.status, parsed.errorList, parsed.serviceContext, parsed.actionName);
    }

    var result = parsed.result;
    result.DP = fixMultiplicity(result.DP);

    return result;
  });
}