import _JSON$stringify from 'babel-runtime/core-js/json/stringify';
import { endpoints, getAbsoluteUrl, fetch } from '../_endpoints';

export { getPaymentStatusForCustomer };

function getPaymentStatusForCustomer(paymentID, bankNumber, bankSystem) {

  var payloadData = {
    GetPaymentStatusForCustomer_Request: {
      PaymentDetails: {
        IBAN: getIbanNumber(bankNumber, bankSystem),
        PaymentId: paymentID
      }
    }
  };

  var opts = {
    method: 'POST',
    body: _JSON$stringify(payloadData)
  };

  return fetch(getAbsoluteUrl(endpoints.PAYMENTS.GET_PAYMENT_STATUS_FOR_CUSTOMER), opts).then(function (response) {
    if (response.ok) {
      return response.json();
    }

    throw new NNServiceError(response.status, endpoints.PAYMENTS.GET_PAYMENT_STATUS_FOR_CUSTOMER);
  });
}

// NN Ibans
function getIbanNumber(bankNumber, bankSystem) {
  if (bankNumber === '0002') {
    return 'NL92INGB0006749661';
  }

  if (bankNumber === '0092') {
    return 'NL72INGB0000157707';
  }

  if (bankNumber === '1010') {
    if (bankSystem === 'L4') {
      return 'NL65INGB0000175875';
    } else {
      return 'NL59INGB0000003032';
    }
  }

  if (bankNumber === '2000') {
    if (bankSystem === 'VD' || bankSystem === 'VG') {
      return 'NL25INGB0000129356';
    } else {
      return 'NL67INGB0000020057';
    }
  }

  return '';
}