import { get, has } from 'lodash';

function createApplicationData(data) {

  if (!has(data, 'GetPortalApplicationData_Response.GetResult.ApplicationData')) {
    // error
    throw Error('Service Error:: getPortalApplicationData');
  }

  return get(data, 'GetPortalApplicationData_Response.GetResult.ApplicationData');
}

export { createApplicationData };