import { get } from 'lodash';

import { fixMultiplicity } from '../../../utils.js';

export { createConsumerCreditDetails };

function createConsumerCreditDetails(data) {

  if (!get(data, 'GetContractDetailsConsumerCredit_Response')) {
    throw new Error('Incorrect body GetAccountsOverview');
  }

  data.GetContractDetailsConsumerCredit_Response.LD.BT = fixMultiplicity(get(data, 'GetContractDetailsConsumerCredit_Response.LD.BT'));

  return data;
}