// NOTE:: for request dont use multiple properties, only single properties
var serviceBody = {
  STATUS: '.Result.Status',
  ERROR: '.Result.ErrorList.Error'
};

/**
 * Utility function to return an absolute url
 *
 * @param {type} endpoint
 * @returns {String}
 */
function getAbsoluteUrl(endpoint) {
  var host = getApiHost(); // 'http://localhost:8080'

  var result = '' + host + endpoint;

  return result;
}

function fetch(url, options) {
  var _fetch = void 0;

  if (getFetchMethod()) {
    // Use custom fetch method when one is provided
    _fetch = getFetchMethod();
  } else {
    _fetch = fetchPonyfill;
  }

  if (getApiHost() === 'mock') {
    url = url.substr(getApiHost().length);

    //Make sure the mock file starts with capital letter

    var file = getMock(url, options);
    var response = {
      ok: true,
      json: function json() {
        return file;
      }
    };
    return _Promise.resolve(response);
  }

  options.headers = options.headers || {};

  if (getAuth()) {
    options.headers = options.headers || {};
    options.headers['authorization'] = 'Basic ' + base64encode(getAuth());
  }

  options.credentials = getFetchCredentials();

  return _fetch(url, options);
}

/**
 * Isomorphic base64 encoder
 *
 * @param {any} str
 * @returns
 */
function base64encode(str) {
  var result = void 0;

  try {
    // Browser
    result = btoa(str);
  } catch (err) {
    // NodeJS
    result = new Buffer(str).toString('base64');
  }

  return result;
}

export { endpoints, serviceBody, getAbsoluteUrl, fetch, base64encode };