// Shared internal function for App calls to getUserDataCustomer with default body
function _getUserDataCustomer(username) {
  var body = {
    UserName: username,
    SubVersion: 1,
    GetAccountData: 'true',
    GetBusinessPartnerData: 'true',
    GetContractList: 'true',
    GetIntermediaryList: 'false',
    GetCallCenterList: 'false',
    CommunicationCentre: 'true',
    GetActionListForChannel: 'Klant-app'
  };

  return customerService.getUserDataCustomer(body).then(function (response) {
    var parsed = new ServiceResponseParser(response);
    if (parsed.status !== ServiceResponseParser.OK) {
      throw new NNAPIError(parsed.status, parsed.errorList, parsed.serviceContext, parsed.actionName);
    }
    return response;
  });
}

function getUserDataCustomer(username) {
  if (!username || typeof username !== 'string') {
    return _Promise.reject(new NNIllegalArgumentError('productOverview.getUserDataCustomer', 'username', username));
  }

  return _getUserDataCustomer(username).then(function (response) {
    var parsed = new ServiceResponseParser(response);
    parsed.result.Body.ActionList.Action = fixMultiplicity(parsed.result.Body.ActionList.Action);
    return parsed.result;
  });
}

