import _JSON$stringify from 'babel-runtime/core-js/json/stringify';
import { endpoints, getAbsoluteUrl, fetch } from '../_endpoints';
import { NNServiceError } from '../error/NNServiceError';
import { getFormDataApi } from '../../config';
import { randomId } from '../../utils/randomId.js';

var REQUEST_FORM_PART = 'metadata';
var FILE_FORM_PART = 'file';

export { archiveDocumentsStreamingForCustomer };

function archiveDocumentsStreamingForCustomer(username, attachment, contractId, productOptions) {

  var payloadData = {
    "ArchiveDocumentsStreaming_Request": {
      "RequestUserId": username,
      "Document": {
        "DocumentClass": productOptions.documentClass,
        "FileRef": 1,
        "MetaData": {
          "ArrivedAt": attachment.arrivedAt,
          "ArrivedBy": username,
          "Contract_Nr": contractId,
          "Contract_NrLoan": contractId,
          "DescriptionProductGroup": productOptions.productGroup,
          "DocumentDetail": productOptions.documentDetail,
          "DocumentID": randomId(),
          "DocumentRouting": productOptions.documentRouting,
          "DocumentTitle": productOptions.documentTitle,
          "DocumentType": productOptions.documentType,
          "Label": "NN",
          "ProductCode": productOptions.productCode,
          "ReferenceDate": attachment.referenceDate,
          "Source": username,
          "SourceTransactionID": randomId()
        }
      },
      "File": {
        "FileRef": 1,
        "MimeType": attachment.mimeType,
        "ExternalFileRef": "1"
      }
    }
  };

  var opts = {
    method: 'POST',
    body: null
  };

  var url = getAbsoluteUrl(endpoints.ARCHIVE_DOCUMENTS_STREAMING_FOR_CUSTOMER);
  var FormData = getFormDataApi();

  var formData = new FormData();
  formData.append(REQUEST_FORM_PART, _JSON$stringify(payloadData));
  formData.append(FILE_FORM_PART, attachment.file, attachment.fileName);

  opts.isFormData = true;
  opts.body = formData;

  return fetch(url, opts).then(function (response) {
    // Check if response is in the 2xx range
    if (response.ok) {
      return response.json().then(function (data) {
        //Add the self generated Doc ID to the response
        data.ArchiveDocumentsStreaming_Response.Result.DocumentID = payloadData.ArchiveDocumentsStreaming_Request.Document.MetaData.DocumentID;
        return data;
      });
    }

    // Request failed (status was not in the 2xx range). Let the application layer deal with this.
    throw new NNServiceError(response.status, endpoints.ARCHIVE_DOCUMENTS_STREAMING_FOR_CUSTOMER);
  });
}