import { get } from 'lodash';

import { fixMultiplicity } from '../../utils.js';

export { createHealthcareDetails };

function createHealthcareDetails(data) {

  if (get(data, 'GetPolicyDetailsHealthInsurance_Response.polis.dekkingen.dekking')) {
    data.GetPolicyDetailsHealthInsurance_Response.polis.dekkingen.dekking = fixMultiplicity(get(data, 'GetPolicyDetailsHealthInsurance_Response.polis.dekkingen.dekking'));
  }

  return data;
}