import _Promise from 'babel-runtime/core-js/promise';
import { genericService } from '../service';
import { genericFactory } from '../factories';
import { NNAPIError } from './error/NNAPIError';
import { NNIllegalArgumentError } from './error/NNIllegalArgumentError';
import { ServiceResponseParser } from './utils/ServiceResponseParser';

function login(username, password) {
    if (!username || typeof username !== 'string') {
        return _Promise.reject(new NNIllegalArgumentError('generic.login', 'username', username));
    }

    if (!password || typeof password !== 'string') {
        return _Promise.reject(new NNIllegalArgumentError('generic.login', 'password', password));
    }

    return genericService.login(username, password).then(function (response) {
        return response.LoginServiceResponse;
    });
}

function putDataForTicketID(application, channel, data) {
    if (!application || typeof application !== 'string') {
        return _Promise.reject(new NNIllegalArgumentError('generic.putDataForTicketID', 'application', application));
    }

    if (!channel || typeof channel !== 'string') {
        return _Promise.reject(new NNIllegalArgumentError('generic.putDataForTicketID', 'channel', channel));
    }

    if (!data || typeof data !== 'string') {
        return _Promise.reject(new NNIllegalArgumentError('generic.putDataForTicketID', 'applicationData', data));
    }

    return genericService.putPortalApplicationData(application, channel, data).then(function (response) {
        var parsed = new ServiceResponseParser(response);
        if (parsed.status !== ServiceResponseParser.OK) {
            throw new NNAPIError(parsed.status, parsed.errorList, parsed.serviceContext, parsed.actionName);
        }

        return genericFactory.createTicketData(response);
    });
}

function getDataForTicketID(ticketId) {
    if (!ticketId || typeof ticketId !== 'string') {
        return _Promise.reject(new NNIllegalArgumentError('generic.getDataForTicketID', 'ticketId', ticketId));
    }

    return genericService.getPortalApplicationData(ticketId).then(function (response) {
        var parsed = new ServiceResponseParser(response);
        if (parsed.status !== ServiceResponseParser.OK) {
            throw new NNAPIError(parsed.status, parsed.errorList, parsed.serviceContext, parsed.actionName);
        }

        return genericFactory.createApplicationData(response);
    });
}

export { login, putDataForTicketID, getDataForTicketID };