import _JSON$stringify from 'babel-runtime/core-js/json/stringify';
import { endpoints, getAbsoluteUrl, fetch } from '../_endpoints';
import { NNServiceError } from '../error/NNServiceError';

export { getPaymentSettings };

function getPaymentSettings(vtaID, contractID) {

  var payloadData = {
    GetPaymentSettings_Request: {
      AL: {
        UserName: 'NNAPP'
      },
      DP: {
        IBAN: contractID,
        VTA_id: vtaID
      }
    }
  };

  var opts = {
    method: 'POST',
    body: _JSON$stringify(payloadData)
  };

  return fetch(getAbsoluteUrl(endpoints.BANK.GET_PAYENT_SETTINGS), opts).then(function (response) {
    // Check if response is in the 2xx range
    if (response.ok) {
      return response.json();
    }

    // Request failed (status was not in the 2xx range). Let the application layer deal with this.
    throw new NNServiceError(response.status, endpoints.BANK.GET_PAYENT_SETTINGS);
  });
}