import { get } from 'lodash';

export { createInvestmentAccountTargetDetails };

function createInvestmentAccountTargetDetails(data) {
  var details = void 0;

  if (get(data, 'GetInvestmentAccountTargetDetails2WithToken_Response.Rendement.Profiel_input')) {

    details = {
      chance: get(data, 'GetInvestmentAccountTargetDetails2WithToken_Response.Rendement.Profiel_input.Kans_op_doelkapitaal'),
      expectedCapital: get(data, 'GetInvestmentAccountTargetDetails2WithToken_Response.Rendement.Profiel_input.TargetCapitalonEndDate'),
      horizon: get(data, 'GetInvestmentAccountTargetDetails2WithToken_Response.Rendement.Profiel_input.Einddatum'),
      capital: get(data, 'GetInvestmentAccountTargetDetails2WithToken_Response.Rendement.Profiel_input.Doelkapitaalkapitaal'),
      prognose: get(data, 'GetInvestmentAccountTargetDetails2WithToken_Response.Rendement.Profiel_input.Prognosetabel_per_jaar'),
      asset: get(data, 'GetInvestmentAccountTargetDetails2WithToken_Response.Portfolio.PortfolioProducts.ProductClassificationTypes.ProductClassificationTypeCollection.ProductClassifications.ProductClassificationWeight')
    };
  } else {
    details = {};
  }

  return details;
}