import { fixMultiplicity } from './fixMultiplicity.js';

function fixNesting(list) {

  list.forEach(function (item) {
    if (item.ContractSubParts && item.ContractSubParts.ContractSubPart) {
      item.subContracts = fixMultiplicity(item.ContractSubParts.ContractSubPart);

      delete item.ContractSubParts;
    }
  });

  // WORKAROUND:: Savings nesting for deposits
  // remove when implemented in service

  // array to place all the savings contracts in that belong together
  var mainSavingsContract;
  // array of item to delete after they are added to the main contract
  var deleteDepositsArr = [];
  // array of product that can have a deposit
  var depositProductCodeArr = ['N300', 'N710', 'N730'];

  list.forEach(function (item, index) {
    if (!depositProductCodeArr.includes(item.PRODUCTCODE)) {
      return;
    }

    // check if next product is deposit
    if (item.VTA_Contract_id.length === 7) {
      // GUDC gives back wrong 'CommercialName' for savings deposit
      item.CommercialName = 'Depositosparen';
      mainSavingsContract.deposits.push(item);
      deleteDepositsArr.push(item);
      return;
    }

    // savingsIndex = index;
    mainSavingsContract = item;
    item.deposits = [];
  });

  list = list.filter(function (item) {
    return !deleteDepositsArr.includes(item);
  });

  return list;
}

export { fixNesting };