// Function to apply a function to lists with nesting
function treeWalker(list, fn) {

  list.forEach(function (item) {
    if (item) {
      fn(item);

      if (item.subContracts) {
        item.subContracts.forEach(function (subitem) {
          fn(subitem);
        });
      }
    }
  });
}

export { treeWalker };