import { endpoints } from '../_endpoints.js';
import { post } from "../_http";

function getCoverageView(companyId, employeeId) {
	var payloadData = {
		"GetCoverageView_Request": {
			"user_organisation_id": companyId,
			"user_id": "admin", // FIXME, hardcoded as requested by the API-team.
			"data_organisation_id": companyId,
			"view_id": employeeId
		}
	};

	return post(endpoints.CORPORATE.GET_COVERAGE_VIEW, payloadData);
}

export { getCoverageView };